import * as React from 'react';
import { createComponent } from '@lit-labs/react';
import type { EventName } from '@lit-labs/react';
import { SideNav as SpSideNav } from '@spectrum-web-components/sidenav';
import { SideNavHeading as SpSideNavHeading } from '@spectrum-web-components/sidenav';
import { SideNavItem as SpSideNavItem } from '@spectrum-web-components/sidenav';

import '@spectrum-web-components/sidenav/sp-sidenav-heading.js';
import '@spectrum-web-components/sidenav/sp-sidenav-item.js';
import '@spectrum-web-components/sidenav/sp-sidenav.js';

export const SideNav = createComponent({
    displayName: 'SideNav',
    elementClass: SpSideNav,
    react: React,
    tagName: 'sp-sidenav',
    events: {
        change: 'change' as EventName<Event>, // Announces a change in the `value` property of the navigation element. This change can be "canceled" via `event.preventDefault()`.
    },
});
export const SideNavHeading = createComponent({
    displayName: 'SideNavHeading',
    elementClass: SpSideNavHeading,
    react: React,
    tagName: 'sp-sidenav-heading',
    events: {},
});
export const SideNavItem = createComponent({
    displayName: 'SideNavItem',
    elementClass: SpSideNavItem,
    react: React,
    tagName: 'sp-sidenav-item',
    events: {},
});

export type SideNavType = EventTarget & SpSideNav;
export type SideNavHeadingType = EventTarget & SpSideNavHeading;
export type SideNavItemType = EventTarget & SpSideNavItem;
